export default {
  transition: 'blog',
  data() {
    return {
      heroAlt: [
        {
          src: '',
          heading: ' Blog ',
          icon: 'mdi-blogger',
        },
      ],
      search: '',
      blogList: [],
    }
  },
  created() {
    this.getAllBlogs()
  },
  methods: {
    async getAllBlogs() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const response = await this.$axios.get(
          'https://cbsbackend.herokuapp.com/api/blogs',
          config
        )
        this.blogList = response.data
      } catch (err) {
        throw new Error('Error Fetching Blogs')
      }
    },
    // BlogStories() {
